<template>
  <div class="buy_sell">
    <div class="wrap">
      <div class="line_up">
        <div class="coin">
          <span class="which_coin">BNB/BTC</span>
          <img class="rotate" src="../../assets/image/up_down.png" alt="">
          <ul class="coin_tab">
            <li>LTB/BTC</li>
            <li>BTH/BTC</li>
            <li>GGB/BTC</li>
          </ul>
        </div>
        <div class="num">0.00022005</div>
        <div class="img"><img src="../../assets/image/table.png" alt=""></div>
      </div>
      <div class="line_down">
        <div class="left">
          <div class="choose">
            <div :class="{active: !isActive}" @click="isActive = !isActive">买入</div>
            <div :class="{active: isActive}" @click="isActive = !isActive">卖出</div>
          </div>
          <div class="tab sell" :class="{active: isActive}">
            <ul>
              <li>可用余额</li>
              <li>0.00000000 {{coin}}</li>
              <li class="btc num">
                <input type="text">
                <span>BTC</span>
              </li>
              <li class="bnb num">
                <span>数量</span>
                <input type="text">
                <span>{{coin}}</span>
              </li>
              <li class="can_sell">
                <span>可卖</span>
                <span>0 <span>{{coin}}</span></span>
              </li>
              <li class="scale">
                <span>比例</span>
                <div class="sliders-bar2">
                  <div class="sliders-bar-inner"></div>
                  <div class="sliders-bar-handle2" id="div2"></div>
                </div>
                <span>{{scale}}%</span>
              </li>
              <li class="money">
                <span>金额</span>
                <span>0.00000000<span> BTC</span></span>
              </li>
            </ul>
            <div class="btn">卖出</div>
          </div>
          <div class="tab buy" :class="{active: !isActive}">
            <ul>
              <li>可用余额</li>
              <li>0.00000000 {{coin}}</li>
              <li class="btc num">
                <input type="text">
                <span>BTC</span>
              </li>
              <li class="bnb num">
                <span>数量</span>
                <input type="text">
                <span>{{coin}}</span>
              </li>
              <li class="can_sell">
                <span>可卖</span>
                <span>0 <span>{{coin}}</span></span>
              </li>
              <li class="scale">
                <span>比例</span>
                <div class="sliders-bar2">
                  <div class="sliders-bar-inner"></div>
                  <div class="sliders-bar-handle2" id="div1"></div>
                </div>
                <span>{{scale}}%</span>
              </li>
              <li class="money">
                <span>金额</span>
                <span>0.00000000<span> BTC</span></span>
              </li>
            </ul>
            <div class="btn">买入</div>
          </div>
        </div>
        <div class="right">
          <table class="fall" :class="{hide: onlySell}">
            <tr>
              <th>价格</th>
              <th>数量</th>
            </tr>
            <tr v-for="item in 8">
              <td>0.00022857</td>
              <td>122</td>
            </tr>
          </table>
          <table class="rise" :class="{hide: onlyBuy}">
            <tr v-for="item in 6">
              <td>0.00022857</td>
              <td>122</td>
            </tr>
          </table>
          <span class="see_buy" @click="onlySell = !onlySell">只看买入</span>
          <span class="see_sell" @click="onlyBuy = !onlyBuy">只看卖出</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'buySell',
  data () {
    return {
      Msg: '买卖',
      rotate1: true,
      isActive: true,
      onlyBuy: false,
      onlySell: false,
      coin: 'BNB',
      Left: 0,
      scale: 0,
      point: null
    }
  },
  components: {
  },
  mounted: function () {
    this.init();
  },
  methods: {
    init () {
      var self = this;
      $(document).ready(() => {
        $('.coin').click(function () {
          $('.coin_tab').slideToggle('fast');
          if(self.rotate1){
            $('.rotate').css('transform', 'rotate(180deg)');
            self.rotate1 = false;
          }else{
            $('.rotate').css('transform', 'rotate(0deg)');
            self.rotate1 = true;
          }
        });
        $('.coin_tab li').click(function(){
          let val = $(this).html();
          let type = val.split('/')[0];
          $('.coin span').html(val);
          self.coin = type;
        });
        self.range($("#div2"));
      })
    },
    range (obj) {
      var self = this;
      obj.on('touchmove', function (e) {
        e.preventDefault();
        var touch = e.originalEvent.targetTouches[0];
        var parentWidth = $(this).parent().width();
        var parentOffWidth = $(this).parent().offset().left;
        var sonWidth = $(this).width();
        var sibling = $(this).siblings();
        self.Left = Math.floor(touch.pageX - parentOffWidth);
        if(self.Left >= 0 && self.Left <= parentWidth - sonWidth){
          this.style.left = self.Left + 'px';
          sibling.width(self.Left);
          self.scale = Math.ceil((sibling.width() / (parentWidth - sonWidth))*100);
        }
      })
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../../assets/style/common.css";
  @mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @mixin font($size, $color: white) {
    font-size: $size;
    font-family: "微软雅黑";
    color: $color;
  }
  .buy_sell {
    position: absolute;
    z-index: 666;
    top: 4rem;
    box-sizing: border-box;
    padding: 0 0.5rem;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    background-color: white;
    .wrap {
      .line_up {
        display: flex;
        padding-bottom: 0.1rem;
        align-items: center;
        border-bottom: 1px solid #d2d2d7;
        & .num {
          flex: 3;
          text-align: center;
        }
        & .img {
          flex: 1;
          text-align: right;
          & > img {
            margin-top: 0.2rem;
            width: 1.2rem;
          }
        }
        & .coin {
          flex: 2;
          position: relative;
          margin: 0.1rem 0;
          text-align: left;
          width: 100%;
          img {
            width: 0.73rem;
            transition: all .3s;
          }
          ul {
            display: none;
            position: absolute;
            top: 1.2rem;
            margin: 0;
            padding-left: 0;
            z-index: 666;
            width: 100%;
            list-style-type: none;
            background-color: white;
            li {
              padding: 0.2rem;
              border-bottom: 1px solid gray;
              &:last-child {
                border: none;
              }
            }
          }
        }
      }
      .line_down {
        display: flex;
        margin-top: 0.3rem;
        & > div {
          flex: 1;
        }
        .left {
          .choose {
            @include font(0.8rem,#f5a445);
            width: 100%;
            display: flex;
            border-radius: 0.3rem;
            border: 1px solid #f5a445;
            & > div {
              flex: 1;
              @include flex;
              padding: 0.3rem 0.5rem;
            }
            .active {
              display: block;
              color: white;
              background-color: #f5a445;
            }
          }
          .tab {
            display: none;
            overflow: hidden;
            @include font(0.7rem,#a6a6b0);
            text-align: left;
            &.active {
              display: block;
            }
            & .btn {
              @include flex;
              @include font(0.8rem,white);
              height: 1.7rem;
              border-radius: 0.3rem;
            }
            &.sell {
              .btn {
                background-color: #ff0100;
              }
            }
            &.buy {
              .btn {
                background-color: #1bb368;
              }
            }
            ul {
              padding: 0;
              list-style-type: none;
              li {
                margin-bottom: 0.3rem;
              }
            }
            .num {
              padding: 0.3rem 0.2rem;
              border-radius: 0.3rem;
              border: 1px solid #a6a6b0;
              input {
                border: none;
                outline: none;
              }
            }
            .btc {
              input {
                width: 75%;
              }
            }
            .bnb {
              input {
                width: 52%;
              }
            }
            .can_sell {
              display: flex;
              justify-content: space-between;
            }
            .scale {
              @extend .can_sell;
              align-items: center;
              .sliders-bar2 {
                position: absolute;
                left: 2.4rem;
                width: 22%;
                height: 6px;
                background:#eeeeee;
                .sliders-bar-inner {
                  position:absolute;
                  left: 0;
                  width: 0;
                  height:100%;
                  background: #f5a445;
                  border-radius:3px;
                }
                .sliders-bar-handle2 {
                  position:absolute;
                  top: -0.2rem;
                  left: -0.3rem;
                  width: 0.6rem;
                  height: 0.6rem;
                  border-radius: 50%;
                  border: 1px solid #c7c7c7;
                  background: #eeeeee;
                  cursor:pointer
                }
              }
            }
            .money {
              @extend .can_sell;
            }
            .dwdw {
              height: 100%;
              display: block;
              &> div {
                height: 100%;
              }
            }
          }
        }
        .right {
          table {
            width: 100%;
            padding-left: 0.5rem;
            @include font(0.7rem,#676767);
            tr {
              text-align: right;
              th:first-child{
                text-align: left;
              }
              td:first-child {
                text-align: left;
                color: #ff0100;
              }
            }
            &.rise {
              margin: 1rem 0 0.4rem 0;
              td:first-child {
                color: #1bb368;
              }
            }
            &.hide {
              display: none;
              overflow: hidden;
            }
          }
          & > span {
            @include font(0.8rem,white);
            &.see_buy {
              color: #1bb368;
            }
            &.see_sell {
              color: #ff0100;
            }
          }
        }
      }
    }

  }

</style>



